import React, { useEffect, ReactNode } from "react";
import {
  Box,
  Stack,
  Typography,
  Paper,
  Avatar,
  Card
} from "@mui/material";
import { MonetizationOn, Security, Healing, Public, Sms, VideoCall } from "@mui/icons-material";

interface FutureItem {
  icon: ReactNode;
  title: string;
  points: string[];
}

interface DocData {
  title: string;
  meta: { version: string; lastUpdated: string };
  future: FutureItem[];
  flowDiagram: { title: string; steps: { icon: ReactNode; label: string }[] };
}

const docData: DocData = {
  title: "跌倒报警产品未来探讨",
  meta: { version: "draft", lastUpdated: "2025-08-16" },
  future: [
    {
      icon: <Healing color="primary" />, title: "健康与流量模块", points: [
        "初期以有特色的小众健康场景为主，例如社区健康活动、康复训练课程。",
        "通过健康场景吸引早期用户，形成可观活跃度。",
        "逐步抽象至更大用户基数的内容，例如常规健康管理、线上健康课程、健康打卡社群。",
        "鼓励老年用户参与探店、娱乐、养生交流，激发老年人大V模态。",
        "通过直播买菜或社区活动增加近场社交互动，并提升安全监护时效。",
        "初期目标偏老人群体，利用UGC内容激活社区互动，避免大量音视频内容起步难。",
        "海外市场可根据发达国家或高收入地区特点选择，消费能力、技术接受度和网络基础设施更适合带货模式。"
      ]
    },
    {
      icon: <MonetizationOn color="secondary" />, title: "广告收益代理模式", points: [
        "预售城市或区县的广告收益权，形式类似众筹或投资。",
        "投资方可选择自投广告，或坐享广告位分成。",
        "可与保安、地铁、红十字等系统合作，给予未来收益作为激励。"
      ]
    },
    {
      icon: <Public color="info" />, title: "海外市场与地区版本", points: [
        "海外市场可能从具体单一市场逐步扩张，也可能采取覆盖大区域如欧洲的版本策略。",
        "针对欧洲的不同语言和监管情况，可能需要特定地区版本。",
        "UGC内容在国外监管一般比国内宽松，但仍需遵守当地法律和平台政策。"
      ]
    }
  ],
  flowDiagram: {
    title: "一次报警多渠道与音视频流程",
    steps: [
      { icon: <Sms color="error" />, label: "多渠道通知 (短信/电话/推送)" },
      { icon: <VideoCall color="success" />, label: "即时多人音视频会议 + 同步地理位置" },
      { icon: <Security color="warning" />, label: "调度安保/医疗人员增援" }
    ]
  }
};

export function Bullets({ items }: { items: string[] }) {
  return (
    <Stack spacing={1}>
      {items.map((t, i) => (
        <Typography variant="body2" key={i} sx={{ ml: 2, lineHeight: 1.6 }}>• {t}</Typography>
      ))}
    </Stack>
  );
}

export function FlowDiagram() {
  return (
    <Paper sx={{ p: 3, mt: 4, borderRadius: 3, backgroundColor: '#f9f9f9' }} elevation={3}>
      <Typography variant="h6" gutterBottom>
        {docData.flowDiagram.title}
      </Typography>
      <Stack direction="column" spacing={4} justifyContent="flex-start">
        {docData.flowDiagram.steps.map((s, i) => (
          <Card key={i} sx={{ p: 2, borderRadius: 2, backgroundColor: '#fff', boxShadow: 1 }}>
            <Stack spacing={1} alignItems="center">
              <Avatar sx={{ bgcolor: '#1976d2' }}>{s.icon}</Avatar>
              <Typography variant="body2" align="center">{s.label}</Typography>
            </Stack>
          </Card>
        ))}
      </Stack>
    </Paper>
  );
}

export function ProdIdeaContent() {
  useEffect(() => {
    window.document.title = docData.title;
  }, []);

  return (
    <Box sx={{ p: 3, maxWidth: 800, mx: 'auto' }}>
      <Typography variant="body2" color="text.secondary" gutterBottom align="center">
        本文档随时会更新，您可以随时重新打开查看。
      </Typography>
      <Typography variant="h4" gutterBottom align="center" sx={{ fontWeight: 700 }}>
        {docData.title}
      </Typography>
      <Stack spacing={5}>
        {docData.future.map((f, i) => (
          <Paper key={i} sx={{ p: 3, borderRadius: 3, backgroundColor: '#f5f5f5' }} elevation={2}>
            <Stack direction="row" spacing={2} alignItems="center" mb={2}>
              <Avatar sx={{ bgcolor: '#1976d2' }}>{f.icon}</Avatar>
              <Typography variant="h6" fontWeight={600}>{f.title}</Typography>
            </Stack>
            <Bullets items={f.points} />
          </Paper>
        ))}
        <FlowDiagram />
      </Stack>
    </Box>
  );
}
